<script>
  import { onMount } from "svelte";
  import Router from "svelte-spa-router";
  import routes from "./routes";

  let visible = true;

  onMount(() => {
    setTimeout(() => {
      visible = true;
    }, 4000);
  });
</script>

{#if visible}
  <div>
    <Router {routes} />
  </div>
{:else}Loading...{/if}
